.underline{
    position: relative;
    margin: 20px 30px;
    float: left;
}
.underline::before {
   
    margin-left: 70px;
    max-width: 140px;
    max-height: 10px;
  content: "";
  position: absolute;
  left: 7%;
  bottom: 16px;

  transform: skew(-12deg) translateX(-50%);
  background: rgba(238,111,87,0.5);
  z-index: -1;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.skillset{
    background-image: url("./image/skillbg.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 300px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.skillset p{
    display: flex;
    justify-content: center;
    
}
@media screen and (max-width:469px){
    .skillset{
        margin: auto;
        margin-top: 10px;
        height: 300px;
        box-shadow: 10px 10px 5px rgb(155, 154, 154);
        border-radius: 20px;
    }
}

