.container{
    display: inline-block;
    margin-bottom:3% ;
    font-size: 20px;

  }
  
.typed {
 
    white-space: nowrap;
    width: 0;
    animation-name: typing;
    animation-duration: 1.5s;
    animation-timing-function: steps(30, end);
    animation-fill-mode: forwards;
    
  }
 
  
    
  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  @media screen and (max-width:769px){
    
      #about{
        width: 350px;
      }
      

    

  }