.underline{
    position: relative;
    margin: 20px 30px;
    float: left;
    margin-right: auto;
}
.underline::before {
    top: 30px;
  content: "";
  position: absolute;
  left: 8%;
  bottom: 16px;
  width: 150px;
  height: 14px;
  transform: skew(-12deg) translateX(-50%);
  background: rgba(238,111,87,0.5);
  z-index: -1;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.input-field {
  position: relative;
}
.input-field input {
  width: 300px;
  height: 55px;
  border-radius: 6px;
  font-size: 18px;
  padding: 0 15px;
  border: 2px solid #5e5d5d;
  background: transparent;
  color: #000000;
  outline: none;
  margin-right: auto;
}
.input-field label {
  position: absolute;
  top: 35%;
  left: 15px;
  transform: translateY(-50%);
  color: #424242;
  font-size: 19px;
  pointer-events: none;
  transition: 0.3s;
  margin-left: 38%;
  
}
input:focus {
  border: 2px solid #000000;
}
input:focus ~ label,
input:valid ~ label {
  top: 0;
  left: 15px;
  font-size: 16px;
  padding: 0 2px;
  background: #fffffffa;
}

.button{
  position: relative;
  background-color: black;
  border-radius: 4em;
  font-size: 16px;
  color: white;
  padding: 0.8em 1.8em;
  cursor:pointer;
  user-select:none;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition-duration: 0.4s;
  -webkit-transition-duration: 0.4s; /* Safari */
}

.button:hover {
  transition-duration: 0.1s;
  background-color: #3A3A3A;
}

.button:after {
  content: "";
  display: block;
  position: absolute;
  border-radius: 4em;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.5s;
  box-shadow: 0 0 10px 40px white;
}

.button:active:after {
  box-shadow: 0 0 0 0 rgb(80, 80, 80);
  position: absolute;
  border-radius: 4em;
  left: 0;
  top:0;
  opacity: 1;
  transition: 0s;
}

.button:active {
  top: 1px;
}
.contact{
  display: flex;
  flex-direction: column;
}
