@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");

*{
    font-family: "poppins",sans-serif;
    
}
body{
  text-transform: capitalize;
}

nav{
  display: flex;
   background-color: rgba(255, 255, 255, 0);
  color: rgb(0, 0, 0);
  position: relative;
  
}
#navbar1{
  display: flex;
  font-weight: 200;
  padding-left: 20px;
  font-size: 30px;
}
#logo{
  border-radius: 47%;
  margin-right: 10px;
  margin: auto;
 

}
#navbar{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 300px;
  float: right;

}
#navbar li{
  list-style: none;
  padding: 0 20px;
  position: relative;
  margin-top: 15px;
 
}
#navbar li a{
  text-decoration: none;
  font-size:1.3rem ;
  font-weight: 200;
  color: #000000;
  animation: 0.3s ease-in-out;
}
#navbar li a:hover{
  color: rgb(85, 2, 2);
}
#navbar li a:hover::after,
#navbar li a.active::after{
  content:"";
  width:30% ;
  height: 2px;
  background-color: rgb(241, 5, 84);
  position: absolute;
  bottom: -2px;
  left: 20px;
}
#mobile i{
  color: #000000;
  float: right;

}
#mobile{
  display: none;
  float: right;
}
.bgImg{
  margin-top: -100px;
}
.info{
  float: left;
  margin-top: -650px;
  position: relative;
  padding-left: 200px;
  
}
@media screen and (max-width:1169px){
 
  #navbar{
      justify-content: flex-start;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      position: fixed;
      top: 90px;
      right: -300px;
      width: 200px;
      height: 80vh;
      background-color: rgb(248, 199, 109);
      padding: 40px 0 0 10px;
      transition: 0.3s ease-in-out;
      margin-top:-65px;

  }
  #navbar.active{
      right: 0px;
  }
  #navbar li{
      margin-bottom: 20px;
     
  }
  #mobile{
      margin-left: auto;
      padding-top: 3px;
      display: block;
      float: right;
  }
  #mobile i{
      font-size: 25px;
      margin-right: auto;
  }
  *{
     
      padding: 0;
      box-sizing: border-box;
      
    }
    .info{
      margin-top: -570px;
    }

  
 

}
@media screen and (max-width:1169px){
  .info{
    margin-top: -470px;
  }
 

}
@media screen and (max-width:1069px){
  .info{
    margin-top: -370px;
    margin-left: -80px;
  }
 

}
@media screen and (max-width:969px){
  .info{
    margin-top: -350px;
    margin-left: -100px;
    font-size: 10px;
  }
 

}
@media screen and (max-width:869px){
  .info{
    margin-top: -240px;
    margin-left: -100px;
    
  }
 

}
@media screen and (max-width:569px){
  .info{
   margin-left: 0%;
   padding-left: 5%;
    margin:auto;
    padding-bottom: 30px;
    font-size: 13px;
   justify-content: start;
    position: static;
    text-align: center;
  }
  .bgImg{
    margin: auto;
    
    
    background-position: center;
    background-size: cover;
  }
  .nav{
    position:absolute; 
    margin-top:10px;   
  }
  
  #mobile{
    margin-left:180px;
  }
 

}
