.box{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background-color:rgb(255, 200, 137) ;
    border-radius: 15px;
    justify-content: center;
    margin: 10px;
}
.boxhead{
    background-color: rgb(255, 144, 125);
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
}
.underline{
    position: relative;
    margin: 20px 30px;
    float: left;
}
.underline::before {
    top: 25px;
  content: "";
  position: absolute;
  left: 8%;
  bottom: 16px;
  width: 150px;
  height: 14px;
  transform: skew(-12deg) translateX(-50%);
  background: rgba(238,111,87,0.5);
  z-index: -1;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.container1{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
    margin-left: auto;
    margin-right: auto;
    
}
#education{
    display: flex;
    flex-direction: column;
}
@media screen and (max-width:469px){
    #education{
        margin-left: auto;
        margin-right: auto;
        width: 300px;
        

    }
    .box{
        box-shadow: 10px 10px 5px rgb(155, 154, 154);
    }
}